@keyframes slideUp {
    0% {
        transform: translateY(100%)
    }

    100% {
        transform: translateY(0)
    }
}

@keyframes slideDown {
    0% {
        transform: translateY(0%)
    }

    100% {
        transform: translateY(100%)
    }
}

.slide-up {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    animation: slideUp .5s ease-in-out;
    z-index: 100
}

.slide-down {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    animation: slideDown .5s forwards ease-in-out;
    z-index: 100
}

.about-me-overlay {
    align-items: center;
    background-color: rgba(0, 0, 0, .897);
    display: flex;
    justify-content: center;
    position: fixed;
    z-index: 10
}

.about-me-background {
    background-image: linear-gradient(rgba(0, 0, 0, .01) 3px, #1b1b1b 2px);
    background-size: 8px 5px;
    height: 100%;
    left: 0;
    overflow-y: auto;
    position: fixed;
    bottom: 10vh;
    width: 100vw;
    z-index: -1;
    height: 80vh;
    overflow-x: hidden;
}

.back-button:before {
    content: "Back"
}

.back-button {
    background-image: linear-gradient(0deg, #b9b9b9 0, #f3f3f3 25% 75%, #b9b9b9 100%);
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 20%/50%;
    font-family: "WiiFont", sans-serif;
    color: #4b4b4b;
    height: 5vh;
    left: 50vw;
    max-width: 35vh;
    position: absolute;
    bottom: 2.5vh;
    text-align: center;
    line-height: 5vh;
    width: 15vh;
    transform: translate(-50%, 0%);
    font-size: 3vh;
    z-index: 140;
    transition: transform .25s ease;
    cursor: pointer
}

.back-button:hover {
    transform: translate(-50%, 0) scale(1.1)
}

.mii-text:before {
    content: "Mii"
}

.mii-text {
    color: #fff;
    font-family: "WiiFont", sans-serif;
    font-size: 6vh;
    position: relative;
    text-align: center;
    z-index: 160;
    content: "Mii";
    top: 2vh;
    left: 37vw
}

.about-me-title {
    font-size: 6vh;
    font-family: "WiiFont", sans-serif;
    color: #fff;
    z-index: 200;
    padding-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center
}

.contact-me-title {
    font-size: 5vh;
    font-family: "WiiFont", sans-serif;
    color: #fff;
    z-index: 200;
    padding-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center
}

.about-me-element-container {
    padding-left: min(15vw, 10vh);
    padding-right: min(15vw, 10vh);
    color: #fff;
    z-index: 200;
    width: min(70vw, 80vh);
    justify-self: center;
    margin: 0 auto; /* Center horizontally */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.linkedin-button {
    display: inline-block;
    background-image: url(../public/assets/icons/linkedin.png);
    background-size: cover;
    width: 15vw;
    height: 15vw;
    transition: transform .25s ease
}

.github-button {
    display: inline-block;
    width: 15vw;
    height: 15vw;
    background-image: url(../public/assets/icons/github.png);
    background-size: cover;
    transition: transform .25s ease
}

.email-button {
    display: inline-block;
    width: 15vw;
    height: 15vw;
    background-image: url(../public/assets/icons/email.png);
    background-size: cover;
    transition: transform .25s ease
}

.email-button:hover,
.github-button:hover,
.linkedin-button:hover {
    transform: scale(1.05)
}

.contact-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    justify-items: center;
    height: 50vh; /* adjust this value to set the desired height */
}

.contact-grid-wrapper {
    max-width: 50%;
    margin: 0 auto;
}

.about-me-markdown {
    color: #fff;
    margin: auto;
    font-size: 2vh;
  }
  
  .about-me-markdown h1,
  .about-me-markdown h2,
  .about-me-markdown h3,
  .about-me-markdown h4,
  .about-me-markdown h5,
  .about-me-markdown h6 {
    color: #fff
  }
  
  .about-me-markdown p,
  .about-me-markdown li {
    color: #fff
  }
  
  .about-me-markdown img {
    display: inline-block
  }
  
  .about-me-markdown code {
    font-size: 1em
  }
  
  .about-me-markdown a {
    color: #00c3ffa4
  }
  
  .about-me-markdown strong {
    padding: 1em;
    border-radius: 5%;
    background: #2e2e2ea8;
    font-family: 'Courier New', monospace
  }