body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

.markdown-content {
  color: #fff;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 2.5vh;
  padding: 1vw 20vw;
}

.toc-parent {
  display: flex;
  justify-content: center; /* Center the child horizontally */
}

.toc-container {
  padding: 2vh 4vh;
  color: #adadad00;
  margin: auto;
  font-size: 1.5vh;
  text-decoration: none; /* Remove underline */
  display: inline-block; /* Occupy only the necessary space */
  /* Additional styles as needed */
}

.grid-container {
  display: grid;
  gap: 1em; /* Adjust the gap between items */
  padding-bottom: 3vh;
  grid-template-columns: repeat(auto-fit, minmax(45%, 1fr)); /* Two columns with a minimum width of 45% each */
}

@media (max-width: 1000px) {
  .grid-container {
    grid-template-columns: 1fr; /* Switch to one column when width is <= 1000px */
  }
}

.left-cell {
  flex: 1; /* Allow the left cell to grow and take up available space */
  max-height: 100%; /* Set max height to match the smaller column's height */
}

.right-cell {
  flex: 1; /* Allow the right cell to grow and take up available space */
  max-height: 100%; /* Set max height to match the smaller column's height */
  overflow: auto; /* Add overflow for content that exceeds max-height */
  background-color: rgba(41, 41, 41, 0.377);
}

a:link {
  text-decoration: none;
  color: rgb(241, 241, 241);
  font-style: italic;
}

a:visited {
  text-decoration: none;
  color: rgb(76, 76, 76);
  font-style: italic;
}

a:hover {
  color: rgb(143, 143, 143);
  font-style: italic;
}

a:active {
  text-decoration: underline;
  color: rgb(0, 150, 137);
  font-style: italic;
}

.markdown-content h1,
.markdown-content h2,
.markdown-content h3,
.markdown-content h4,
.markdown-content h5,
.markdown-content h6 {
  color: #fff;
}

.markdown-content p,
.markdown-content li {
  color: #fff;
}

.markdown-content strong {
  padding: 1px;
  border-radius: 0%;
  background: #333333a8;
  font-family: "Courier New", monospace;
}

.fade-in-blogpost {
  opacity: 0;
  animation: fadeIn 0.3s forwards ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.blog-title-selected {
  font-size: 6vh;
  font-family: "WiiFont", sans-serif;
  color: #fff;
  padding-top: 3vh;
  padding-bottom: 3vh;
  text-align: center;
  margin-top: 2vh; /* Adjust this value to move it further down */
}

.TOC-title {
  font-size: 4vh;
  font-family: "WiiFont", sans-serif;
  color: #ffffff;
  text-align: center;
}

.date-selected {
  font-size: 3vh;
  font-family: "WiiFont", sans-serif;
  color: #fff;
  text-align: center;
  padding: 1.5vh;
}

.blog-tagline-selected {
  font-size: 3vh;
  font-family: "WiiFont", sans-serif;
  color: #fff;
  padding-top: 1vh;
  padding-left: 3vh;
  padding-right: 3vh;
}

.horizontal-line {
  padding-top: 2vh;
  border-top: 5px solid #fff;
}

.blog-post-tags {
  padding-top: 2vh;
  display: flex;
  justify-content: center;
}

.blog-post-tag-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.blog-post-tag {
  background-color: #33333379;
  padding: 8px 12px;
  border-radius: 4px;
  text-align: center;
  font-size: 2vh;
  color: #ddd;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .markdown-content {
    font-size: 2vh;
    padding: 1vw 10vw;
  }

  .blog-title-selected {
    font-size: 5vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
  }

  .TOC-title,
  .blog-tagline-selected {
    font-size: 2.5vh;
    padding: 0.5vh;
  }

  .date-selected {
    font-size: 2vh;
  }

  .horizontal-line {
    padding-top: 1.5vh;
    border-top: 3px solid #fff;
  }

  .blog-post-tag-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .markdown-content {
    font-size: 2vh;
    padding: 1vh 5vh;
  }

  .blog-title-selected {
    font-size: 5vh;
    padding-top: 1vh;
    padding-bottom: 1vh;
  }

  .TOC-title,
  .blog-tagline-selected {
    font-size: 2.5vh;
    padding: 0.5vh;
  }

  .date-selected {
    font-size: 2vh;
  }

  .horizontal-line {
    padding-top: 1vh;
    border-top: 2px solid #fff;
  }

  .blog-post-tag-grid {
    grid-template-columns: 1fr;
  }

  .blog-post-tag {
    font-size: 2vh;
    padding: 6px 10px;
  }
}


@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap');

.code-block {
  position: relative;
  margin-bottom: 1rem;
  font-family: 'Fira Code', monospace;
}

.long-code .code-content {
  max-height: 25vh;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
}

.long-code.scrollable .code-content {
  overflow-y: auto;
}

.long-code .code-content.expanded {
  max-height: none;
}

.code-toggle {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #444;
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 0.8rem;
  z-index: 1;
  font-family: 'Fira Code', monospace;
}

.code-toggle:hover {
  background-color: #555;
}

.code-content pre {
  margin: 0;
}

.code-content code {
  font-family: 'Fira Code', monospace !important;
  font-size: 14px;
  line-height: 1.5;
}

/* Styling for the scrollbar */
.long-code.scrollable .code-content::-webkit-scrollbar {
  width: 8px;
}

.long-code.scrollable .code-content::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.long-code.scrollable .code-content::-webkit-scrollbar-thumb {
  background: #888;
}

.long-code.scrollable .code-content::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.markdown-content code {
  font-size: 1.5vh;
}


.winxp-window-container {
  text-align: center;
}

.winxp-window {
  background: #ECE9D8;
  border: 1px solid #0054E3;
  border-radius: 8px 8px 0 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  display: inline-block;
  overflow: hidden;
}

.winxp-title-bar {
  background: linear-gradient(to bottom, #0058E6, #3A93FF);
  color: white;
  font-family: 'Tahoma', sans-serif;
  font-size: 1.25vh;
  font-weight: bold;
  padding: 2px 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.winxp-title {
  display: flex;
  align-items: center;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
}

.winxp-favicon {
  width: 1.75vh;
  height: 1.75vh;
  margin-right: 0.5vh;
  object-fit: contain;
}

.winxp-filename {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%; /* Adjust based on favicon width and margin */
}

.winxp-buttons {
  display: flex;
}

.winxp-button {
  width: 1.25vh;
  height: 1.25vh;
  margin-left: 2px;
  border: 1px solid #fff;
  border-radius: 0.5vh;
}

.winxp-minimize {
  background: #FFD500;
}

.winxp-maximize {
  background: #00CA00;
}

.winxp-close {
  background: #FF0000;
}

.winxp-content {
  line-height: 0;
}

.winxp-content img {
  display: block;
  max-width: 100%;
  height: auto;
}


.blog-word-count-centered {
  font-size: 1.5vh;
  color: #888;
  text-align: center;
  margin-top: 2vh;
  font-family: 'Tahoma', sans-serif;
}

.summary-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
